<template><div class="markdown-body"><h1>面包屑导航</h1>
<p><code v-pre="">mu-breadcrumbs</code> 组件是一个页面辅助导航器。它可以使用图标或文字符作为分隔符。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>默认情况下使用文本分隔符，通过设定 <code v-pre="">divider</code> 属性改变。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-breadcrumbs&gt;\n    &lt;mu-breadcrumbs-item v-for=\&quot;item in items\&quot; :key=\&quot;item.text\&quot; :disabled=\&quot;item.disabled\&quot;&gt;{{item.text}}&lt;/mu-breadcrumbs-item&gt;\n  &lt;/mu-breadcrumbs&gt;\n  &lt;mu-breadcrumbs divider=\&quot;—\&quot;&gt;\n    &lt;mu-breadcrumbs-item v-for=\&quot;item in items\&quot; :key=\&quot;item.text\&quot; :disabled=\&quot;item.disabled\&quot;&gt;{{item.text}}&lt;/mu-breadcrumbs-item&gt;\n  &lt;/mu-breadcrumbs&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      items: [\n        {\n          text: 'Dashboard',\n          disabled: false\n        },\n        {\n          text: 'Link 1',\n          disabled: false\n        },\n        {\n          text: 'Link 2',\n          disabled: true\n        }\n      ]\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-breadcrumbs>
    <mu-breadcrumbs-item v-for="item in items" :key="item.text" :disabled="item.disabled">{{item.text}}</mu-breadcrumbs-item>
  </mu-breadcrumbs>
  <mu-breadcrumbs divider="—">
    <mu-breadcrumbs-item v-for="item in items" :key="item.text" :disabled="item.disabled">{{item.text}}</mu-breadcrumbs-item>
  </mu-breadcrumbs>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs-item</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in items"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.text"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"item.disabled"</span>&gt;</span>{{item.text}}<span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs</span> <span class="hljs-attr">divider</span>=<span class="hljs-string">"—"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs-item</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in items"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.text"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"item.disabled"</span>&gt;</span>{{item.text}}<span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">items</span>: [
        {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Dashboard'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>
        },
        {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Link 1'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>
        },
        {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Link 2'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
        }
      ]
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="tu-biao-fen-ge-fu"><a class="header-anchor" href="#tu-biao-fen-ge-fu" aria-hidden="true">¶</a> 图标分隔符</h2>
<p>通过 <code v-pre="">divider</code> slot 定制不同的分隔符。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-breadcrumbs&gt;\n    &lt;mu-icon value=\&quot;forward\&quot; slot=\&quot;divider\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;mu-breadcrumbs-item v-for=\&quot;item in items\&quot; :key=\&quot;item.text\&quot; :disabled=\&quot;item.disabled\&quot;&gt;{{item.text}}&lt;/mu-breadcrumbs-item&gt;\n  &lt;/mu-breadcrumbs&gt;\n  &lt;mu-breadcrumbs divider=\&quot;—\&quot;&gt;\n    &lt;mu-icon value=\&quot;chevron_right\&quot; slot=\&quot;divider\&quot;&gt;&lt;/mu-icon&gt;\n    &lt;mu-breadcrumbs-item v-for=\&quot;item in items\&quot; :key=\&quot;item.text\&quot; :disabled=\&quot;item.disabled\&quot;&gt;{{item.text}}&lt;/mu-breadcrumbs-item&gt;\n  &lt;/mu-breadcrumbs&gt;\n&lt;/mu-container&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      items: [\n        {\n          text: 'Dashboard',\n          disabled: false\n        },\n        {\n          text: 'Link 1',\n          disabled: false\n        },\n        {\n          text: 'Link 2',\n          disabled: true\n        }\n      ]\n    }\n  }\n};\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-container>
  <mu-breadcrumbs>
    <mu-icon value="forward" slot="divider"></mu-icon>
    <mu-breadcrumbs-item v-for="item in items" :key="item.text" :disabled="item.disabled">{{item.text}}</mu-breadcrumbs-item>
  </mu-breadcrumbs>
  <mu-breadcrumbs divider="—">
    <mu-icon value="chevron_right" slot="divider"></mu-icon>
    <mu-breadcrumbs-item v-for="item in items" :key="item.text" :disabled="item.disabled">{{item.text}}</mu-breadcrumbs-item>
  </mu-breadcrumbs>
</mu-container>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-container</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"forward"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"divider"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs-item</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in items"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.text"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"item.disabled"</span>&gt;</span>{{item.text}}<span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs</span> <span class="hljs-attr">divider</span>=<span class="hljs-string">"—"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"chevron_right"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"divider"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-breadcrumbs-item</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in items"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.text"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"item.disabled"</span>&gt;</span>{{item.text}}<span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-breadcrumbs</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">items</span>: [
        {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Dashboard'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>
        },
        {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Link 1'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">false</span>
        },
        {
          <span class="hljs-attr">text</span>: <span class="hljs-string">'Link 2'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
        }
      ]
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="breadcrumbs-props"><a class="header-anchor" href="#breadcrumbs-props" aria-hidden="true">¶</a> Breadcrumbs Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>divider</td>
<td>分隔符</td>
<td>String</td>
<td>—</td>
<td>‘/’</td>
</tr>
</tbody>
</table>
<h2 id="breadcrumbs-slots"><a class="header-anchor" href="#breadcrumbs-slots" aria-hidden="true">¶</a> Breadcrumbs Slots</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
</tr>
</thead>
<tbody>
<tr>
<td>divider</td>
<td>分隔符插槽，设置之后会覆盖 <code v-pre="">divider</code> 属性.</td>
</tr>
<tr>
<td>default</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="breadcrumbs-item-props"><a class="header-anchor" href="#breadcrumbs-item-props" aria-hidden="true">¶</a> Breadcrumbs Item Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用此导航，一般用于面包屑导航的最后一个</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>href</td>
<td>相当于 a 标签的 href属性，设置之后会渲染成 a 标签，默认是使用button标签</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>to</td>
<td>表示目标路由的链接。设置之后组件会被渲染成<code v-pre="">router-link</code>, 当被点击后，内部会立刻把 <code v-pre="">to</code> 的值传到 <code v-pre="">router.push()</code>，所以这个值可以是一个字符串或者是描述目标位置的对象。</td>
<td>String/Object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>tag</td>
<td>有时候想要 <code v-pre="">&lt;router-link&gt;</code> 渲染成某种标签，例如 <code v-pre="">&lt;li&gt;</code>。 于是我们使用 <code v-pre="">tag prop</code> 类指定何种标签，同样它还是会监听点击，触发导航。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>active-class</td>
<td>设置 链接激活时使用的 CSS 类名。默认值可以通过路由的构造选项 <code v-pre="">linkActiveClass</code> 来全局配置。</td>
<td>String</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>event</td>
<td>声明可以用来触发导航的事件。可以是一个字符串或是一个包含字符串的数组</td>
<td>String/Array</td>
<td>—</td>
<td>click</td>
</tr>
<tr>
<td>exact</td>
<td>“是否激活” 默认类名的依据是 <strong>inclusive match</strong> （全包含匹配）。 举个例子，如果当前的路径是 <code v-pre="">/a</code> 开头的，那么 <code v-pre="">&lt;router-link to=&quot;/a&quot;&gt;</code> 也会被设置 CSS 类名。</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>exact-active-class</td>
<td>配置当链接被精确匹配的时候应该激活的 class。注意默认值也是可以通过路由构造函数选项 linkExactActiveClass 进行全局配置的。</td>
<td>String</td>
<td>—</td>
<td>router-link-exact-active</td>
</tr>
<tr>
<td>append</td>
<td>设置 <code v-pre="">append</code> 属性后，则在当前（相对）路径前添加基路径。例如，我们从 <code v-pre="">/a</code> 导航到一个相对路径 b，如果没有配置 append，则路径为 /b，如果配了，则为 /a/b</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>replace</td>
<td>设置 <code v-pre="">replace</code> 属性的话，当点击时，会调用 <code v-pre="">router.replace()</code> 而不是 <code v-pre="">router.push()</code>，于是导航后不会留下 history 记录。</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      items: [
        {
          text: 'Dashboard',
          disabled: false
        },
        {
          text: 'Link 1',
          disabled: false
        },
        {
          text: 'Link 2',
          disabled: true
        }
      ]
    }
  }
}
</script>


